.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

// google fonts
@import url('https://fonts.googleapis.com/css2?family=Barlow&family=Merienda&display=swap');

//variables;
$primaryColor: #040404;
$secondaryColor: #AFEDFF;
$tertiaryColor: #FFB0C6;
$quaternaryColor: #9BD694;

$primaryFont: 'Barlow', sans-serif;
$secondaryFont: 'Merienda', cursive;

//mixins
@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  font-family: $primaryFont;
  color: $primaryColor;
  background: $secondaryColor;
}

header {
  @include center;
  height: 100px
}

main {
  @include center;
  min-height: calc(100vh - 100px)
}

.button {
  margin: 50px;
}








